@import "../../styles/colors";

@media screen and (max-width: 470px) {
  .logo {
    display: flex; }
  .navlink {
    margin: auto 5px; }
  .navbar {
    justify-content: center; } }

@media screen and (min-width: 470px) {
  .logo {
    display: flex; }
  .navlink {
    margin: auto 20px; }
  .navbar {
    justify-content: space-between; } }

.navbar {
  display: flex;
  width: 100%;
  height: 50px;
  border-radius: 0 0 15px 15px;
  background-color: $primary;
  margin-bottom: 5px;
  position: sticky;
  top: 0;
  z-index: 1; }

.navmenu {
  display: flex;
  margin: auto 0; }

.navlink {
  text-decoration: none;
  color: $primary-text;
  transition-duration: 300ms; }

.navlinkActive {
  @extend .navlink;
  color: white; }

.logo {
  justify-content: center;
  align-items: center; }
